

.experienceTop {
    display:flex;
    align-items: center;
    justify-content: center;
    margin: 4rem 0;
    overflow-y: scorll;
}

::-webkit-scrollbar {
    width: 5px;
    height: 3px;
}

::-webkit-scrollbar-track {
    background-color: white;
}

::-webkit-scrollbar-thumb {
    background-color: white;
}

.experienceWrapper {
    display:flex;
    padding: 70px;
    width: 70%;
}


.experienceLeft,
.experienceRight {
    flex:1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    
} 



.techImg {
  width: 24rem;
  height: 24rem;
  object-fit: contain;
  margin-right: 30px;
}



.description {
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
    font-size: 18px;
}

.experienceBottom {
    display:flex;
    align-items: center;
    justify-content: center;
    position:relative;
    height: 100%;

}

.site {
    margin: 0 10px;
}


.experienceIcon {
    height: 100%;
    bottom: 0;
    position:absolute;
}
.address {
    color: black;
    margin-left: 15px;
}

.address:hover {
    color:grey;
    text-decoration: none;
}
