

.centerTop {
    
    display:flex;
    align-items: center;
    justify-content: center;
    padding: 50px;
    margin: 50px 0;
    overflow-y:scroll;
}

::-webkit-scrollbar {
    width: 5px;
    height: 3px;
}

::-webkit-scrollbar-track {
    background-color: white;
}

::-webkit-scrollbar-thumb {
    background-color: white;
}

.centerTopWrapper {
    padding: 70px;
    width:70%;
    
}


.objective {
    font-weight: bold;
    font-size: 28px;
    text-align: left;
    margin: 30px 0;
}

.education {
    font-size: 16px;
    font-weight: 300;
    text-align: right;
    margin:0;
}

.centerBottom {
    display:flex;
    align-items: center;
    justify-content: center;
    position:relative;
    height: 100%;
}

.email {
    display:flex;
    align-items: center;
    justify-content: center;
    width:100%; 
    color: grey;
    bottom: 0;
    position:absolute;
}