

.registerWrapper {
    padding: 50px;
    margin: 20px 0;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}


.desc {
    text-align: center;
    font-size: 20px;
}

.registerBox {
    height: 200px;
    padding: 20px;
    display:flex;
    flex-direction: column;
    justify-content: space-between;
}

.infoInput {
    height: 30px;
    border-radius: 5px;
    border: 1px solid gray;
    padding-left: 10px;
}

.infoInput:focus {
    outline:none
}

.loginButton {
    display:flex;
    align-items: center;
    justify-content: center;
    height: 25px;
    border: 5px;
    cursor: pointer;
    font-size: 13px;
}

.loginButton:focus {
    outline: none;
}