

.evaluateTop {
    padding: 50px;
    display:flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
}

.desc {
    font-weight: bold;
    font-size: 28px;
    margin: 30px 0;
}


.thumbs {
    display:flex;
}

.thumb {
    margin: 0 30px;
    background-color: #EEEEEE;
    border-radius: 10px;
    padding: 0 20px;
}

.thumbIcon {
    font-size: 60px !important;
    color:#1597E5;
    cursor: pointer;
}