

.postContainer {
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 50px;
}

.postVideo {
    width: 70%;
    border-radius: 10px;
    -webkit-box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
    box-shadow: 0px 0px 16px -8px rgba(0, 0, 0, 0.68);
    margin: 30px 0;
}


.videoWrapper {
    padding: 60px;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.projectInfo {
    font-family: 'Bebas Neue', cursive;
    font-size: 24px;
    margin: 20px 50px;
    text-align: center;

}

